/* TODO: Implement responsive design */

.header{
    top: 0;
    left: 0;
    width: 100%;

}

.icon{
    vertical-align: middle;
}


.right-side li{
    list-style-type: none;
}

@media all and (min-width: 730px) {
    .header{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 4rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 10%;
        box-sizing: border-box;
    }
    .right-side li{
        float: left;
        height: auto;
        vertical-align: middle;
        line-height: 5rem;
    }
    
    .right-side *{
        margin: 0 .3em;
    }

}



/* push the content below the header down 4rem to not make the header overlap: */
.header + *{
    margin-top: 4rem;
}

a{
    text-decoration: none;
    font-weight: bold;
    color: #f8f8ff;
}


